@import url(https://fonts.googleapis.com/css2?family=Creepster&family=Monoton&display=swap);
.App {
    width: 100vw;
    height: 100vh;
    background-image: url(/Portfolio/static/media/bg.0ab80169.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}

header {
    box-shadow: 0 0 10px white;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}

#title {
    font-family: 'Monoton', cursive;
    letter-spacing: 0.6rem;
}

#cardsHolder {
    margin-top: 10vh;
}

@media only screen and (max-width:1000px) {
    #cardsHolder {
        margin-top: 0vh;
    }
}

#searchInput {
    background-color: transparent;
    color: white;
}

.navItems:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.dropdown-menu {
    background-color: rgba(255, 255, 255, 0.2)!important;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.navDropdownItem {
    color: white!important;
}

.navDropdownItem:active {
    color: white!important;
}

.navDropdownItem:hover {
    background-color: rgba(0, 0, 0, 0.5)!important;
}

#searchInput:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

footer {
    position: absolute;
    bottom: 0px;
    text-align: center;
    width: 100%;
    padding: 10px;
}

footer>div {
    display: inline-block;
    padding: 10px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    border-radius: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    bottom: 0%;
}

.custom-icon {
    margin: 0 10px;
}
#card {
    margin: 10px;
    display: inline-block;
    background: transparent;
    color: white;
    width: 24rem;
    border: none;
    box-shadow: 0px 0px 10px black;
    transition-duration: 300ms;
}

#card:hover {
    transform: scale(1.012);
    box-shadow: 0px 0px 20px black;
}

.cardBody {
    transition: -webkit-backdrop-filter 1000ms;
    transition: backdrop-filter 1000ms;
    transition: backdrop-filter 1000ms, -webkit-backdrop-filter 1000ms;
}

#card:hover>.cardBody {
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}

@media only screen and (max-width:720px) {
    #card {
        width: 300px;
    }
    #cardImg {
        width: 300px;
    }
    .cardBody {
        width: 300px;
    }
}
.cardsHolder {
    display: block;
    width: 100%;
    text-align: center;
}
